import DrawFlow from "@/components/DrawFlow/index.vue";
import AdminLayout from "@/layouts/Admin/index.vue";
import { defineComponent } from "vue";
import Loading from "vue3-loading-overlay";
import "vue3-loading-overlay/dist/vue3-loading-overlay.css";
import BlocksMenu from "@/components/BlocksMenu/index.vue";
import { useStore } from "vuex";
import routes from "@/routes";
import EventEmitter from "@/util/EventEmitter";
import BotoModal from "@/components/BotoModal/index.vue";
import "vue-toast-notification/dist/theme-sugar.css";

export default defineComponent({
  name: "App",
  setup() {
    const store = useStore();
    return {
      store,
    };
  },
  data() {
    return {
      open: false,
      blocks: null,
      fullPage: true,
      authToken: null,
      bot: null,
      isLoading: true,
      activationShow: false,
      activationLoading: false,
      saveError: false,
      saveErrorText: "",
      saveSuccess: false,
      saveLoading: false,
      isFlowValid: false,
      usesTemplate: false,
      editorAreaHeight: 0,
      showingToast: false,
    };
  },
  components: {
    AdminLayout,
    Loading,
    DrawFlow,
    BlocksMenu,
    BotoModal,
  },
  beforeUnmount() {
    this.bot = null;
  },
  methods: {
    showToast(type: string, message: string) {
      this.$toast.open({
        message,
        type,
        duration: 5000,
        dismissible: true,
        position: "top-right",
      });
    },
    cleanSave() {
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const vm = this;
      setTimeout(function () {
        vm.saveLoading = false;
        vm.saveSuccess = false;
        vm.saveError = false;
        vm.saveErrorText = "";
      }, 3000);
    },
    cancelSaveBot() {
      this.bot = null;
      this.isFlowValid = false;
      this.store.commit("removeTemplate");
      // @ts-expect-error ignore
      this.$refs.drawflowEditor.clearBot();
      routes.push({ name: "home.editor", params: {} });
    },
    cancelActivateBot() {
      this.activationShow = false;
    },
    tagRemovalFromField(blockId: string) {
      if (!this.showingToast) {
        this.showingToast = true;
        this.showToast(
          "warning",
          `Some fields have been removed on block #${blockId} as data from previous blocks is not available anymore`
        );
        setTimeout(() => {
          this.showingToast = false;
        }, 1000);
      }
    },
    async activateBot() {
      try {
        this.activationLoading = true;
        const data = await this.switchBoto("ON");
        routes.push({
          name: "home.botos",
          params: {
            botId: (this.bot as any).id,
            botName: (this.bot as any).name,
            botFrequency: (this.bot as any).frequency,
          },
        });
      } catch (e) {
        console.error(e);
        this.showToast("error", "There was a problem trying to activate this bot.");
        this.activationLoading = false;
      }
      this.activationShow = false;
    },
    async saveBot() {
      try {
        if (this.isFlowValid) {
          this.saveLoading = true;
          this.saveSuccess = false;
          this.saveError = false;
          this.saveErrorText = "";
          // @ts-expect-error ignore
          const jsonData = this.$refs.drawflowEditor.export();
          const method = this.bot && !this.usesTemplate ? "PUT" : "POST";
          let url = "https://botoapp.io/api/bots/";
          // @ts-expect-error ignore
          if (this.bot && !this.usesTemplate) url = `${url}${this.bot?.id}/`;
          const requestOptions = {
            method,
            headers: {
              "Content-Type": "application/json",
              Authorization: `Token ${this.authToken}`,
            },
            body: JSON.stringify(jsonData),
          };
          const response = await fetch(url, requestOptions);
          if (!response.ok) {
            throw new Error(`An error has occured: ${response.status}`);
          }
          if (!this.bot || (this.bot && this.usesTemplate)) this.activationShow = true;
          this.store.commit("removeTemplate");
          this.usesTemplate = false;
          const data = await response.json();
          this.bot = data;
          this.saveLoading = false;
          this.saveSuccess = true;
          this.cleanSave();
        }
      } catch (e) {
        this.showToast("error", "There was a problem trying to save this bot.");
        this.activationLoading = false;
        this.saveLoading = false;
        this.saveSuccess = false;
        this.saveError = true;
        this.saveErrorText = e;
        this.cleanSave();
      }
    },
    async switchBoto(status: string) {
      try {
        const headers = new Headers();
        const formdata = new FormData();
        // @ts-expect-error ignore
        const url = `https://botoapp.io/api/bot/${this.bot!.id}/`;
        // @ts-expect-error ignore
        formdata.append("name", this.bot!.name);
        formdata.append("status", status);
        // @ts-expect-error ignore
        formdata.append("frequency", this.bot!.frequency.toString());
        const requestOptions = {
          method: "PUT",
          headers,
          body: formdata,
        };
        headers.append("Authorization", `Token ${this.authToken}`);
        const response = await fetch(url, requestOptions);
        if (!response.ok) {
          throw new Error(`An error has occured: ${response.status}`);
        }
      } catch (e) {
        this.showToast("error", "There was a problem trying to activate this bot.");
        this.activationLoading = false;
        console.error(e);
      }
    },
    async getBlocks() {
      const response = await fetch("https://botoapp.io/api/blocks/", {
        cache: "no-store",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Token ${this.authToken}`,
        },
      });
      const data = await response.json();
      return data;
    },
    async getBot(id: string) {
      const response = await fetch(`https://botoapp.io/api/bot/${id}/`, {
        cache: "no-store",
        headers: {
          "Content-Type": "application/json",
          Authorization: `Token ${this.authToken}`,
        },
      });
      const data = await response.json();
      return data;
    },
  },
  async mounted() {
    try {
      this.authToken = this.store.getters.localStorageAuthToken;
      const template = this.store.getters.localStorageTemplate;
      const blocks = await this.getBlocks();
      this.blocks = blocks.results.filter(function (el: any) {
        return el.published === true;
      });
      const botId = this.$route.params.id;
      if (botId) {
        const bot = await this.getBot(botId.toString());
        this.bot = bot;
        this.usesTemplate = false;
      } else if (template) {
        this.bot = JSON.parse(template);
        this.usesTemplate = true;
        // copied bot
        if (this.bot && (this.bot as any).copy) {
          this.showToast("success", "Your bot has been copied!");
          delete (this.bot as any).copy;
        }
      }
      this.isLoading = false;
      // eslint-disable-next-line @typescript-eslint/no-this-alias
      const vm = this;
      EventEmitter.emitter.on("editor-valid-status", function (status: boolean) {
        vm.isFlowValid = status;
      });
      EventEmitter.emitter.on("block-properties-tag-removal", this.tagRemovalFromField);
      setTimeout(() => {
        // @ts-expect-error ignore
        this.editorAreaHeight = this.$refs.editorContainer.clientHeight - 50;
      }, 100);
    } catch (e) {
      this.showToast("error", "There was a problem trying to load this bot.");
      this.isLoading = false;
    }
  },
});
