import { defineComponent } from "vue";

export default defineComponent({
  name: "MenuBlockComponent",
  props: {
    id: Number,
    blockId: String,
    data: Object,
  },
  methods: {
    drag(ev: any) {
      ev.dataTransfer.setData("node", ev.target.getAttribute("data-node"));
      ev.dataTransfer.setData("data", JSON.stringify(this.data));
    },
  },
});
