import MenuBlock from "@/components/MenuBlock/index.vue";
import MenuBlockPlaceHolder from "@/components/PlaceHolders/MenuBlockPlaceHolder/index.vue";
import "vue-skeletor/dist/vue-skeletor.css";
import { defineComponent } from "vue";
import { Block } from "@/interfaces/Block";

const filters: string[] = [];
const items: Block[] = [];

export default defineComponent({
  name: "BlocksMenuComponent",
  props: {
    blocks: Object,
    height: Number,
  },
  components: {
    MenuBlock,
    MenuBlockPlaceHolder,
  },
  watch: {
    blocks: {
      immediate: true,
      handler(val) {
        this.items = val;
        this.filteredItems = val;
        this.isLoadingData = false;
      },
    },
  },
  data() {
    return {
      items,
      isLoadingData: false,
      filters,
      filteredItems: items,
    };
  },
  created() {
    this.isLoadingData = true;
  },
  methods: {
    updateData(data: Block[]) {
      this.items = data;
      this.filteredItems = data;
      this.isLoadingData = false;
    },
    // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
    addFilter(filter: string) {
      const find = this.filters.findIndex((element) => element === filter);
      if (this.filters.find((element) => element === filter)) {
        this.filters.splice(find, 1);
      } else {
        this.filters.push(filter);
      }
      const filtered = this.items.filter((el: Block) => this.filters.indexOf(el.block_type) > -1);
      this.filteredItems = this.filters.length > 0 ? filtered : this.items;
    },
  },
});
